      RegionRank: 3,
      Support: MSP.Support.YesWithCaveats,
      Statement:
        "We in the SNP oppose LGBT conversion therapy practices, which are discriminatory and proven to have a harmful effect on the mental health and wellbeing of LGBT people. SNP MPs will press the UK government to reform laws and regulations, which are reserved to Westminster, to ban and prevent the practice of conversion therapy. It is important to recognise that such therapy is without any evidential basis, unsurprisingly, given that most of the research findings indicate an adverse impact on peoples mental health, rather than a cure. The SNP Government does not support or advocate the practice of conversion therapy anywhere in the UK, and fully support moves to ban it. Some of my SNP MP colleagues participated in the recent Westminster Hall debate on the e-petition 300976 relating to LGBT conversion therapies, pressing the UK Government to ban the practice. My colleague and Women and Equalities Spokesperson Kirsten Oswald MP said during the debate: Let us be clear: LGBT people do not need their identities debated nor do they need to be converted. That is fundamental. Nobodys identity should be subject to debate or to change by other people. I trust that you find this response reassuring that I and my SNP colleagues share your concerns about this abominable practice."
